* {
  margin: 0;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.App {
  font-family: sans-serif;
  user-select: none;
}
::-webkit-scrollbar {
  display: none;
}
#focus {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  color: white;
  font-size: 6rem;
  font-weight: bold;
  text-transform: uppercase;
  text-shadow: 1px 1px 5px black;
  padding: 10px;
  transition: 1s;
  z-index: 2;
}
#image {
  width: 100vw;
  height: 100vh;
  transform: scale(1.5);
  overflow: hidden;
  transition: 1s;
  filter: blur(5px);
}
